import React from 'react';
import {CWidgetBrand} from '@coreui/react';
import {CIcon} from '@coreui/icons-react';
import { cibTwitter } from "@coreui/icons";
React.icons = {cibTwitter, ...React.icons}

export default ()=>{
  /*example start: script*/
  return (
    <div className={'row mt-2'}>
      <div className={'col-6'}>
        <CWidgetBrand
          color="twitter"
          rightHeader="973k"
          rightFooter="followers"
          leftHeader="1.792"
          leftFooter="tweets"
        >
          <CIcon
            name="cibTwitter"
            height="52"
            className="my-4"
          />
        </CWidgetBrand>
      </div>
    </div>
  )
  /*example end: script*/
}
