import React from 'react';
import {CIconRaw} from '@coreui/icons-react'
import {cilUser, cilSettings} from '@coreui/icons';
React.icons = {cilUser, cilSettings, ...React.icons}

export default ()=>{

  /*example start: script*/
  return (
    <>
      <CIconRaw width="48" name={'cilUser'} />
      <CIconRaw width="72" name={'cilSettings'} />
    </>
  )
  /*example end: script*/
}
