import React from 'react';
import { CWidgetProgressIcon } from "@coreui/react";
import {CIcon} from '@coreui/icons-react';
import {cilSettings} from '@coreui/icons';
React.icons = {cilSettings, ...React.icons}

export default ()=>{
  /*example start: script*/
  return (
    <div className={'row mt-2'}>
      <div className={'col-6'}>
        <CWidgetProgressIcon
          header="87.500"
          text="Visitors"
          color="gradient-info"
        >
          <CIcon name="cilSettings" size="xl"/>
        </CWidgetProgressIcon>
      </div>
    </div>
  )
  /*example end: script*/
}
