import React from 'react';
import {CForm, CFormGroup, CFormText, CInputGroup, CInputGroupPrepend, CInputGroupText, CInput, CInputGroupAppend, CLabel} from '@coreui/react';
import {CIcon} from '@coreui/icons-react';
import {cilUser} from '@coreui/icons';
React.icons = {cilUser, ...React.icons}

export default ()=>{
  /*example start: script*/
  return (
    <div className={'mt-2 col-6'}>
      <CInputGroup>
        <CInputGroupPrepend>
          <CInputGroupText className={'bg-info text-white'}>
            Username
          </CInputGroupText>
        </CInputGroupPrepend>
        <CInput type="email" id="username" name="username" autoComplete="name"/>
        <CInputGroupAppend>
          <CInputGroupText className={'bg-info text-white'}>
            <CIcon name={'cilUser'}/>
          </CInputGroupText>
        </CInputGroupAppend>
      </CInputGroup>
    </div>
  )
  /*example end: script*/
}
