import React from 'react';
import {CAlert} from '@coreui/react';

export default ()=>{

  /*example start: script*/
  return (
    <div className="mt-2">
      <CAlert color="primary">CAlert</CAlert>
      <CAlert color="secondary">CAlert</CAlert>
      <CAlert color="info" closeButton>
        I am an dismissible alert!
      </CAlert>
    </div>
  )
  /*example end: script*/

}
