import React, { useState } from 'react';
import {CInputCheckbox, CForm, CFormGroup, CLabel, CInvalidFeedback, CValidFeedback} from '@coreui/react';

export default ()=>{

  /*example start: script*/
  const [checked, setChecked] = useState(false)

  return (
    <div>
      <CForm>
        <CFormGroup variant="custom-checkbox" className="form-group">
          <CInputCheckbox
            custom
            id="accept"
            required
            valid={checked}
            invalid={!checked}
            onChange={() => setChecked(!checked)}
          >
          </CInputCheckbox>
          <CLabel variant="custom-checkbox" htmlFor="accept">I accept the terms of use</CLabel>
          <CInvalidFeedback className="help-block">
            Please check the agreement
          </CInvalidFeedback>
          <CValidFeedback className="help-block">Terms accepted</CValidFeedback>
        </CFormGroup>
      </CForm>
    </div>
  )
  /*example end: script*/
}
